import Vue from 'vue'
import Router from 'vue-router'
const Index = () => import ( /* webpackChunkName: "Index" */ 'pages/Index') // 路由推崇这种写法按需加载 中间的webpackChunkName 为分包可要可不要 官方文档有
import Login from 'pages/Login'
import Terms from 'pages/Terms'
import Privacy from 'pages/Privacy'
import Transtion from 'pages/Transtion'
import Prompt from 'pages/Prompt'
import AddMember from 'pages/AddMember'
import Policyholder from 'pages/Policyholder'
import Summary from 'pages/Summary'
import GeneralInfo from 'pages/GeneralInfo'
import Address from 'pages/Address'
import YourOrder from 'pages/YourOrder'
import Payment from 'pages/Payment'
// import Payments from 'pages/Payments'
import Complete from 'pages/Complete'
import CoverageDetails from 'pages/CoverageDetails'
import Failed from 'pages/Failed'
import Declarations from 'pages/Declarations'
import PersonalInformation from 'pages/PersonalInformation'







Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index/:merchantId/:productId/:language',
      name: 'index',
      component: Index,
      
    },
    {
      path: '/Login',
      name: 'login',
      component: Login
    },
    {
      path: '/Terms',
      name: 'terms',
      component: Terms
    },
    {
      path: '/Privacy',
      name: 'privacy',
      component: Privacy
    },
    {
      path: '/Transtion',
      name: 'transtion',
      component: Transtion
    },
    {
      path: '/Prompt',
      name: 'prompt',
      component: Prompt
    },
    {
      path: '/AddMember',
      name: 'addMember',
      component: AddMember
    },
    {
      path: '/Policyholder',
      name: 'policyholder',
      component: Policyholder
    },
    {
      path: '/GeneralInfo',
      name: 'generalInfo',
      component:GeneralInfo
    },
    {
      path: '/Summary',
      name: 'summary',
      component: Summary
    },
   
    {
      path: '/Address',
      name: 'address',
      component:Address
    },
   
    {
      path: '/YourOrder',
      name: 'yourOrder',
      component: YourOrder
    },
    {
      path: '/Payment',
      name: 'payment',
      component: Payment
    },
    // {
    //   path: '/Payments',
    //   name: 'payments',
    //   component: Payments
    // },
    {
      path: '/Complete',
      name: 'complete',
      component: Complete
    },
    {
      path: '/CoverageDetails',
      name: 'coverageDetails',
      component: CoverageDetails
    },
    {
      path: '/Failed',
      name: 'failed',
      component: Failed
    },
    {
      path: '/Declarations',
      name: 'declarations',
      component: Declarations
    },
    {
      path: '/PersonalInformation',
      name: 'personalInformation',
      component: PersonalInformation
    }
  ],
  afterEach(to,from,next) {
   return {x:0,y:0} //切换路由之后滚动条始终在最顶部
  }
})



