<template>
  <div class="page-wrap">
    <div class="page-wraps">
      <div class="headTitle">
        <i @click="toPrePage" class="cubeic-arrow"></i>
        <p>{{$t('hl_insurance_start_notes1')}}</p>
      </div>
      <div class="head-imgs">
        <img class="head-img" :src="this.data.logo_path" alt />
      </div>

      <div class="Starr">
        <div :class="{'desc':flag}"  v-html="this.data.desc1"></div>
        <span class="more" @click="clickmore">{{flag ? $t('product_decs_key_more') : $t('hide')}}</span>
      </div>

      <div class="individual" v-html="this.data.descHtml"></div>
    </div>
    <div class="button-bar">
      <button class="proceed" @click="enter">{{$t('confirm')}}</button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      more: false,
      data: "",
      flag: true
    };
  },
  created() {
    
    // toast.show();
  },
  methods: {
    clickmore() {
      this.flag = !this.flag;
    },
    enter() {
      this.$router.push("/AddMember");
    },
    toPrePage() {
      this.$router.push("/Login");
    },
     consoleReplaceUrl(text) {
     // var re= /(http||https:\/\/)*?[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+\.?\/*?([a-zA-Z0-9][-a-zA-Z0-9]{0,62})\/*?([a-zA-Z0-9][-a-zA-Z0-9]{0,62})/g
     var re=/(\b(https?|ftp|file):\/\/[-A-Z0-9+&@#\/%?=~_|!:,.;]*[-A-Z0-9+&@#\/%=~_|])/ig
     var s = text.replace(re, function(a) {
        return '<a href="' + a + '" target=_blank style="color:#0c81ff;text-decoration:underline">' + a + "</a>";
      });
      console.log(s);
      return s;
    }
  },
  computed: {
    ...mapState(["language", "product_id"]),
   
  },
  mounted() {
    
    // const loading = this.$createToast({
    //   time: 0,
    //   txt: this.$t("loading")
    // });
    var params = {
      product_id: this.product_id,
      language: this.language
    };

    this.$http
      .post("/wesupromo/web/tpc/getImportantNotes", params)
      .then(res => {
        this.data = res.data;
        console.log(this.data);
      this.data.desc1= this.consoleReplaceUrl(res.data.desc1)

          //    this.$nextTick(() => {
          //   loading.hide();
          // });
      });
  }
};
</script>

<style lang="less" scoped>
@deep: ~'>>>';

@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  .page-wraps {
    // padding-bottom: 300px;
    .headTitle {
      position: fixed;
      height: 100px;
      top: 0;
      width: 100%;
      left: 0;
      z-index: 99;
      background-color: rgba(253, 253, 253);
      border-bottom: 2px solid rgba(211, 209, 209, 0.25);
      .cubeic-arrow {
        color: rgb(0, 122, 255);
        display: inline-block;
        transform: rotateZ(180deg);
        font-size: 36px;
        font-weight: bold;
        position: absolute;
        left: 10px;
        line-height: 100px;
        width: 50px;
        text-align: center;
        height: 100%;
      }
      p {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: inline-block;
        font-size: 35px;
        font-weight: 600;
        margin-top: 40px;
      }
    }
    .head-imgs {
      width: 428px;
      height: 194px;
      margin: 0 auto;
      margin-top: 90px;
      .head-img {
        width: 100%;
        height: 100%;
      }
    }
    .Starr {
      margin-top: 40px;
      border-bottom: 1px solid rgb(209, 208, 208);
      padding-bottom: 30px;
      white-space: pre-line;
      position: relative;
      font-size: 28px;
      line-height: 30px;
      color: rgb(74, 74, 74);
    }
    .desc {
      font-size: 28px;
      color: rgb(74, 74, 74);
      line-height: 30px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      /* autoprefixer:off */
      -webkit-box-orient: vertical;
      /* autoprefixer:on */
      -webkit-line-clamp: 6;
    }
    .more {
      color: @blue;
      position: absolute;
      right: 0;
      bottom: 10px;
    }
    .individual {
      margin-top: 30px;
      padding-left: 18px;
      padding-bottom: 30px;
      font-size: 28px;
      color: rgb(74, 74, 74);
      line-height: 32px;
      margin-bottom: 5px;
    }
  }
  .button-bar {
    // position: absolute;
    text-align: center;
    // bottom: 0px;
    // left: 50%;
    // transform: translateX(-50%);
    // background: white;
    width: 100%;
    height: 270px;
    // border-top: 2px solid rgba(211, 209, 209, 0.25);
margin-top: 40px;
  }
  .proceed {
    margin-top: 60px;
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
 @{deep} a{
  color:#0c81ff;
  text-decoration:underline;
}
//媒体查询
@media screen and (min-width: 1050px) {
  .page-wrap {
    width: 900px;
    margin: 0 auto;
    box-sizing: border-box;
    .page-wraps {
      .headTitle {
        width: 900px;
        margin-left: 50%;
        transform: translateX(-50%);
      }
    }
    .button-bar {
      width: 900px;
    }
  }
}
</style>


