<template>
    <div class="page-wrap">
       
         <div class="run">
           <img src="@/assets/images/run-man-run-min.gif" alt="">
         </div>
         <div class="imgs">
           <span class="wes">
             <img src="@/assets/images/wesuranse 3-01@3x.png" alt="">
           </span>  
           <span class="dev">
             <img :src=this.progressLogo alt="">
            </span>
         </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data () {
      return{
          count:3,//倒计时
          timer: null,
          data:""
      }
  },
computed:{
    ...mapState(['travelInfo', 'product_id','areacode','progressLogo']),

},
  created() {
    this.countDown();
  },

  methods: {
    countDown() {
      if(!this.timer) {
        this.timer = setInterval(() => {
          if(this.count > 0) {
            this.count = this.count -1
          } else {
            clearInterval(this.timer);
            this.timer = null;
            //跳转的页面写在此处
            this.$router.push({
              path: '/Prompt'
            });
          }
        }, 1000);
      }
    }
  },

  beforeDestroy () {
    if(this.timer) { 
        clearInterval(this.timer);
        this.timer = null;
    }
  },
 
  
}
 
</script>

<style lang="less" scoped>
  @import '~style/mixin.less';
 .page-wrap{
     padding: 34px;
     .run{
       width: 420px;
       height: 420px;
       margin: 0 auto;
       margin-top: 150px;
       img{
         width: 100%;
         height: 100%;
       }
     }
     .imgs{
     display: flex;
     justify-content: space-between;
     .wes{
      display: inline-block;
      width: 200px;
      height: 200px;
     
      text-align: center;
      border-radius: 50%;
      box-shadow: 0px 8px 40px 0 rgba(0, 0, 0, 0.14);
      img{
        width: 90%;
        height: 90%;
        margin-top: 13px;
      }
     }
     .dev{
      display: inline-block;
      width: 200px;
      height: 200px;
      text-align: center;
      border-radius: 50%;
      box-shadow: 0px 8px 40px 0 rgba(0, 0, 0, 0.14);

      img{
        width: 80%;
        height: 80%;
        margin-top: 20px;
      }
      }
     }
     

 }
 //媒体查询
@media screen and (min-width: 1050px) {
  .page-wrap {
    width: 900px;
    margin: 0 auto;
  }
}
</style>


