<template>
  <div class="page-wrap">
    <div class="page-wraps">
      <div class="headTitle">
        <i @click="toPrePage" class="cubeic-arrow"></i>
        <p>{{$t('firm_order_title')}}</p>
      </div>
      <div class="plan-item">
        <p class="plan">{{$t('firm_order_plan_name')}}</p>
        <p class="travelead">{{this.name}}</p>
      </div>

      <div class="pre-item">
        <span>
          <p class="premium">{{$t('firm_order_policy_year')}}</p>
          <p class="price">{{this.plan.period_value}}{{plans}}</p>
        </span>
        <span>
          <p class="premium">{{$t('firm_order_premium')}}</p>
          <p class="price">{{this.currency_desc}}{{this.NumFormat(this.data.premium)}}</p>
        </span>
      </div>

      <div class="date">
        <span>
          <p class="date-title">{{$t('firm_order_star_date')}}</p>
          <p class="date-date">{{starTime}}</p>
        </span>
        <span>
          <p class="date-title">{{$t('endDate')}}</p>
          <p class="date-date">{{endDate}}</p>
        </span>
      </div>
      <!-- 投保人 -->
       <div class="members">
        <span>
          <p class="memb">
            {{$t('policyholder')}}
            <!-- <span>1</span> -->
          </p>
          <p>{{$t('relationship')}}</p>
          <p>{{$t('Gender')}}</p>
          <p v-if="this.policyholder.form.surname">{{$t('account_info_lastname')}}</p>
          <p>{{$t('Given_Name_English')}}</p>
          <!-- <p>{{$t('area_code')}}</p>
          <p>{{$t('phone_number')}}</p>
          <p>{{$t('login_email_r')}}</p> -->
          <p>{{$t('account_info_birthday')}}</p>
          <p>{{policyholderNames}}</p>
        </span>
        <span class="members-right">
          <p class="edit" @click="edit('/Policyholder')">{{$t('firm_order_change')}}</p>
          <p>{{$t('policyholder')}}</p>
          <p>{{this.policyholder.form.gender}}</p>
          <p v-if="this.policyholder.form.surname">{{this.policyholder.form.surname}}</p>
          <p>{{this.policyholder.form.givename}}</p>
          <!-- <p>{{policyCode}}</p>
          <p>{{this.policyholder.form.phoneNumber}}</p>
          <p>{{this.policyholder.form.email}}</p> -->
          <p>{{this.policyholder.form.datebirth}}</p>
          <p>{{this.policyholder.form.hkid}}</p>
        </span>
      </div>

<!-- 受保人 -->
       <div class="members">
        <span>
          <p class="memb">
            {{$t('insured_person')}}
            <!-- <span>1</span> -->
          </p>
          <p>{{$t('relationship')}}</p>
          <p>{{$t('Gender')}}</p>
          <p>{{$t('account_info_lastname')}}</p>
          <p>{{$t('Given_Name_English')}}</p>
          <!-- <p>{{$t('area_code')}}</p>
          <p>{{$t('phone_number')}}</p>
          <p>{{$t('login_email_r')}}</p> -->
          <p>{{$t('account_info_birthday')}}</p>
          <p>{{addMemberNames}}</p>
        </span>
        <span class="members-right">
          <p class="edit" @click="edit('/AddMember')">{{$t('firm_order_change')}}</p>
          <p>{{this.inPerson}}</p>
          <p>{{this.addMember.form.gender}}</p>
          <p>{{this.addMember.form.surname}}</p>
          <p>{{this.addMember.form.givename}}</p>
          <!-- <p>{{addmemberCode}}</p>
          <p>{{this.addMember.form.phoneNumber}}</p>
          <p>{{this.addMember.form.email}}</p> -->
          <p>{{this.addMember.form.datebirth}}</p>
          <p>{{this.addMember.form.hkid}}</p>
        </span>
      </div>
      <!-- 调用组件 -->
      <!-- <WOrder></WOrder> -->
     
    </div>
    <div class="button-bar">
      <button class="proceed" @click="Confirm">{{$t('confirm')}}</button>
    </div>
  </div>
</template>

<script>
import WOrder from "components/WOrder.vue";
import { mapState, mapMutations } from "vuex";

export default {
  // components: {
  //   WOrder
  // },
  data() {
    return {
      data: ""
    };
  },
  created() {
  
    //报价
    var params = {
      product_id: this.product_id,
      language: this.language,
      company_plan_id: this.plan.company_plan_id,
      others_insured_list: [
        {
          dob: this.addMember.form.datebirth
        }
      ]
    };
    this.$http
      .post("/wesupromo/web/product/getMedicalInsuranceProductQuote", params)
      .then(res => {
        console.log(res);
        this.data = res.data;
      });

    //獲取公鑰
    this.$http
      .post("/wesupromo/web/getpublicKey", {
        id: this.userId
      })
      .then(res => {
        console.log(res);
        if (res.status == 100) {
          this.setCommonKey(res.data.public_key);
        }
      });
  },
  methods: {
    ...mapMutations(["setOrderDate", "setCommonKey"]),
    //编辑按钮
    edit(path) {
      // this.$router.push(path);
       this.$router.push({ 
        path: path,
        query: {isChange: 'true'}});
    
    },
     // 金额显示.00格式
    NumFormat (value) {
      if(!value) return '0.00'
      value = value.toFixed(2)
      var intPart = Math.trunc(value)// 获取整数部分
      var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,') // 将整数部分逢三一断
      var floatPart = '.00' // 预定义小数部分
      var value2Array = value.split('.')
      // =2表示数据有小数位
      if(value2Array.length === 2) {
        floatPart = value2Array[1].toString() // 拿到小数部分
        if(floatPart.length === 1) { // 补0,实际上用不着
          return intPartFormat + '.' + floatPart + '0'
        } else {
          return intPartFormat + '.' + floatPart
        }
      } else {
        return intPartFormat + floatPart
      }
    },
    Confirm() {
      const time = {
        starTime: this.starTime,
        endTime: this.endDate
      };
      this.setOrderDate(time);
      this.$router.push("/Payment");
    },
    toPrePage() {
      this.$router.push('Address');
    },
     //结束时间
    getEndDate(date, month, year) {
      console.log(date);
      var date2 = null;
      //判断加了三个月之后是否大于12月
      var years = Number(date[0]) + year;
      var months = Number(date[1]) + month;
      console.log(years, months);
      if (months > 12) {
        months = months - 12;
        years = Number(date[0]) + 1;
      }
      date2 = [
        years.toString(),
        months.toString(),
        date[2] < 10 ? "0" + date[2] : date[2]
      ].join("/");
      console.log(date2);
      console.log(new Date(date2));
      var times = new Date(`${date2} 00:00:00`).getTime() - 1; //转成时间戳
      var date = new Date(times);
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      console.log(Y + M + D);
      return Y + M + D;
    }
  },
  computed: {
    ...mapState([
      "plan",
      "addMember",
      "address",
      "orderDate",
      "product_id",
      "language",
      "currency_desc",
      "userId",
      "commonKey",
      "policyholder",
      "name",
      "inPerson"
    ]),
    starTime() {
      var date = new Date();
      var month =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return date.getFullYear() + "-" + month + "-" + day;
    },
    // endTime() {
    //   var date = new Date();
    //   var month =
    //     date.getMonth() + 1 < 10
    //       ? "0" + (date.getMonth() + 1)
    //       : date.getMonth() + 1;
    //   var day =
    //     date.getDate() < 10 ? "0" + date.getDate() - 1 : date.getDate() - 1;
    //     day=day < 10 ? "0" + day : day;
    //     console.log(day)
    //   return date.getFullYear() + 1 + "-" + month + "-" + day;
    // },
    plans() {
      let plans = "";
      if (this.plan.period_type == 2) {
        if(this.plan.period_value > 1){
          plans=this.$t('firm_order_years')
        }else{
           plans = this.$t("firm_order_year");
        }       
      } else if (this.plan.period_type == 1) {
       
        plans = this.$t("Days");
      } else {
        if(this.plan.period_value>1){
          plans=this.$t('months')
        }else{
          plans = this.$t("month");
        }
        
      }
      return plans;
    },
    addMemberNames() {
      let addMemberNames = "";
      if (this.addMember.form.certificatesType == 0) {
        addMemberNames = this.$t("no_KTP_number");
      } else {
        addMemberNames = this.$t("personal_passport");
      }
      return addMemberNames;
    },
    policyholderNames(){
let policyholderNames = "";
      if (this.policyholder.form.certificatesType == 0) {
        policyholderNames = this.$t("no_KTP_number");
      } else {
        policyholderNames = this.$t("personal_passport");
      }
      return policyholderNames;
    },
//     policyCode(){
//  if(this.policyholder){
// var str = this.policyholder.form.areaCode;
//       console.log(str)
//       str=str.split(" +")
//        console.log(str)      
//       str = "+" +str[1];
//       return str;
//       }
//     },
//     addmemberCode(){
//  if(this.addMember){
// var str = this.addMember.form.areaCode;
//       console.log(str)
//       str=str.split(" +")
//        console.log(str)      
//       str = "+" +str[1];
//       return str;
//       }
//     },
    //结束日期
    endDate() {
      var start = new Date()
        .toISOString()
        .slice(0, 10)
        .split("-");
      console.log(start);
      if (this.plan.period_type == 2) {
        return this.getEndDate(start, 0, this.plan.period_value);
      } else if(this.plan.period_type == 3){
        return this.getEndDate(start, this.plan.period_value, 0);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  .page-wraps {
    // padding-bottom: 300px;
    .headTitle {
      position: fixed;
      height: 100px;
      top: 0;
      width: 100%;
      left: 0;
      z-index: 99;
      background-color: rgba(253, 253, 253);
      border-bottom: 2px solid rgba(211, 209, 209, 0.25);
      .cubeic-arrow {
        color: rgb(0, 122, 255);
        display: inline-block;
        transform: rotateZ(180deg);
        font-size: 36px;
        font-weight: bold;
        position: absolute;
        left: 10px;
        line-height: 100px;
        width: 50px;
        text-align: center;
        height: 100%;
      }
      p {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: inline-block;
        font-size: 35px;
        font-weight: 600;
        margin-top: 40px;
      }
    }
    .plan-item {
      margin-top: 90px;
      line-height: 40px;
      .plan {
        color: #9b9b9b;
        letter-spacing: 1px;
        // font-family: PingFangHK-Regular;
      }
      .travelead {
        // font-family: PingFangHK-Regular;
        font-size: 36px;
      }
    }
    .pre-item {
      line-height: 40px;
      margin-top: 40px;
      display: flex;
      > span {
        margin-right: 25px;
      }
      .premium {
        color: #9b9b9b;//rgb(206, 206, 206);
        // font-family: PingFangHK-Regular;
      }
      .price {
        color: @blue;
        font-weight: 700;
        font-size: 40px;
      }
    }
    .date {
      display: flex;
      justify-content: space-between;
      margin-top: 100px;
      line-height: 45px;
      span {
        .date-title {
          color:#9b9b9b;
          // font-family: PingFangHK-Regular;
        }
        .date-date {
          font-size: 30px;
        }
      }
      .edit {
        color: @blue;
      }
    }
    .members {
      display: flex;
      justify-content: space-between;
      margin-top: 50px;
      span {
        line-height: 40px;
        .memb {
          color:#9b9b9b;
          margin-bottom: 20px;
        }
        .edit {
          margin-bottom: 20px;
          text-align: right;
          color: @blue;
        }
      }
      .members-right {
        text-align: right;
      }
    }
  }
  .button-bar {
    // position: fixed;
    text-align: center;
    // bottom: 0px;
    // left: 50%;
    // transform: translateX(-50%);
    background: white;
    width: 100%;
    height: 270px;
    // border-top: 2px solid rgba(211, 209, 209, 0.25);
margin-top: 40px;
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
    margin-top: 50px;
  }
}
//媒体查询
@media screen and (min-width: 1050px) {
  .page-wrap {
    width: 900px;
    margin: 0 auto;
    box-sizing: border-box;
    .page-wraps {
      .headTitle {
        width: 900px;
        margin-left: 50%;
        transform: translateX(-50%);
      }
    }
    .button-bar {
      width: 900px;
    }
  }
}
</style>


